import {Component, OnInit} from '@angular/core';
import {SettingService} from './services/setting.service';
import {Response} from '@angular/http';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  isSubscribed: boolean = false;
  isActivated: boolean = false;
  showLoader: boolean = true;
  currentSysId: string;
  activationState: string;

  haveSubscribed(dataEvent: { isSubscribed: boolean }) {
    this.isSubscribed = dataEvent.isSubscribed;
    // this.showLoader = false;
    this.activationState = 'submited';
    // this.getActivation();
    console.log('this.isSuscribed', this.isSubscribed);
  }

  haveActivated(dataEvent: { isActivated: boolean }) {
    // this.isActivated = dataEvent.isActivated;
    // this.showLoader = false;
    this.activationState = 'activated';
   // this.getActivation();
    console.log('this.isActivated', this.isActivated);
  }

  constructor(private settingService: SettingService) {
  }

  ngOnInit() {
    this.getActivation();
  }

  getActivation() {
    this.showLoader = true;
    this.settingService.checkActivation()
      .subscribe(
        (response: Response) => {
          let data = response.json();
          if (data.result && data.result[0] && data.result[0].activation_state) {
            console.log('-- isSubscribed defined as:', data.result[0].activation_state);
            this.activationState = data.result[0].activation_state;
            this.showLoader = false;
            this.settingService.changeSysId(data.result[0].sys_id);
            // this.isSubscribed = true;
            // setTimeout(() => {
            //   this.settingService.changeSysId(data.result[0].sys_id);
            // }, 2000);

          } else {
            this.activationState = '';
            // this.isSubscribed = false;
            this.showLoader = false;
          }
        },
        (err) => {
          console.log('some error when activation check:', err);
        }
      );
  }
}



