import {Component, OnInit, ViewChild, Inject} from '@angular/core';
import {FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import {MatDialog} from '@angular/material';
import {DeviceDialogComponent} from './device-dialog/device-dialog.component';
import {SettingService} from '../services/setting.service';
import {ServerDataService} from '../services/server-data.service';
import {UserService} from '../services/user.service';
import {Response} from '@angular/http';
import {MatStepper} from '@angular/material';

import {Observable} from 'rxjs/Observable';
import 'rxjs/add/operator/startWith';
import 'rxjs/add/operator/map';

@Component({
  selector: 'app-body-stepper',
  templateUrl: './body-stepper.component.html',
  styleUrls: ['./body-stepper.component.css']
})
export class BodyStepperComponent implements OnInit {
  @ViewChild('stepper') stepper;
  // @ViewChild('stepper') stepper: MatStepper
  isLinear = true;
  firstFormGroup: FormGroup;
  secondFormGroup: FormGroup;
  thirdFormGroup: FormGroup;
  fourthFormGroup: FormGroup;
  fifthFormGroup: FormGroup;
  currentSysId: string;
  loader1 = false;

  deviceCtrl: FormControl;
  filteredDevice: Observable<any[]>;

  catalogDevices: any = [];
  catalogPlans: any = {};
  catalogFeatures: any = {};

  catalog: {};

  devices = [];

  roles: any [];
  users = [];
  token: string;
  goodToken = false;


  constructor(public dialog: MatDialog,
              private _formBuilder: FormBuilder,
              private globalSettings: SettingService,
              private _serverDataService: ServerDataService,
              private userSys: UserService) {
    this.deviceCtrl = new FormControl();
    this.filteredDevice = this.deviceCtrl.valueChanges
      .startWith(null)
      .map(state => state ? this.filterDevice(state) : this.catalogDevices.slice());
  }

  filterDevice(name: string) {
    return this.catalogDevices.filter(state =>
      state.name.toLowerCase().indexOf(name.toLowerCase()) === 0);
  }


  ngOnInit() {
    this.globalSettings.currentSysId.subscribe(currentSysId => this.currentSysId = currentSysId);

    this.userSys.getUsers().subscribe(response => this.users = response.result);
    this.userSys.getRoles().subscribe(response => this.roles = response.result);

    this.firstFormGroup = this._formBuilder.group({
      // firstCtrl: ['', Validators.required]
    });
    this.secondFormGroup = this._formBuilder.group({
      // firstCtrl: ['', Validators.required],
      // secondCtrl: ['', Validators.required]
    });
    this.thirdFormGroup = this._formBuilder.group({
      // thirdCtrl: ['', Validators.required]
    });
    this.fourthFormGroup = this._formBuilder.group({
      // fourthCtrl: ['', Validators.required]
    });
    this.fifthFormGroup = this._formBuilder.group({
      // firstCtrl: ['']
    });
  }

  saveBillingData(dataEvent: { billingInfo: object }) {

    // console.log('saveBillingData at parent COntroller' );
     const billingInfo = dataEvent.billingInfo;
    // if (isTokenApplied) {
    //

    //this.globalSettings.setBillingInfo(billingInfo).subscribe();
    this.stepper.selected.completed = true;
    this.stepper.selectedIndex = 3;
      // console.log('saveBillingData at parent COntroller', billingInfo);
    // }
  }


  isTokenReady(dataEvent: { tokenApplied: boolean }) {
    this.getData();
    const isTokenApplied = dataEvent.tokenApplied;
    if (isTokenApplied) {
      this.stepper.selected.completed = true;
      this.stepper.selectedIndex = 1;
      this.getData();
      console.log('stepper', this.stepper);
    }
  }

  openDialog(item): void {
    const dialogRef = this.dialog.open(DeviceDialogComponent, {
      height: '90vh',
      width: '800px',
      data: item
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
    });
  }


  checkToken() {
    // ==========  Temp ===============
    // this.firstFormGroup.disable();
    // this.secondFormGroup.disable();
    // this.thirdFormGroup.disable();
    // this.fourthFormGroup.disable();
    // this.fifthFormGroup.disable();
    // =============================

    //this.loader1 = false;
    //setTimeout(() => {
    //  this.stepper.selectedIndex = 1;
    //}, 1500);

     //if (this.token && this.token.length > 5) {
     //  this.loader1 = true;
     //  // this.firstFormGroup.disable();
     //  this.stepper.selected.editable = false;
     //
     //  this.globalSettings.setToken(this.token, this.currentSysId)
     //    .subscribe(
     //      () => {
     //        this.getData();
     //        this.goodToken = true;
     //        this.loader1 = false;
     //        setTimeout(() => {
     //          this.stepper.selectedIndex = 1;
     //        }, 1500);
     //      },
     //      () => {
     //        this.loader1 = false;
     //      }
     //    )
     //}
  }

  getData() {
    this.globalSettings.pullCatalog()
      .subscribe(
        (response: Response) => {
          // let data = response.json();
          // console.log('all Catalog:', data.result[0].catalog);
          // this.catalog = JSON.parse(data.result[0].catalog)
          // console.log('sku11460700036:', this.catalog);
          // this.catalog = data.result;
          //this.createCatalogs();
        },
        (err) => {
          console.log('some error whit catalog loading:', err);
        },
      );
  }

  chooseDevice(device) {
    this.devices.push(device)
    let i = this.devices.indexOf(device)
    this.setOptionsDevice(i)
    // console.log('device', device)

  }

  setOptionsDevice(i) {
    let device = this.devices[i];
    let devicePlans = [];
    for (let plan of device.plans.rate) {
      devicePlans.push(this.catalogPlans[plan]);
    }
    for (let plan of device.plans.data) {
      devicePlans.push(this.catalogPlans[plan]);
    }
    this.devices[i].devicePlans = devicePlans;
  }

  removeDevice(i) {
    this.devices.splice(i, 1)
  }

  createCatalogs() {
    for (let i in this.catalog) {
      let item = this.catalog[i];
      switch (item.type) {
        case 'deviceSku': {
          this.catalogDevices.push(item);
          break;
        }
        case 'rateplanSku': {
          item.isVisible = false;
          this.catalogPlans[item.sku] = item;
          break;
        }
        case 'featureSku': {
          this.catalogFeatures[item.sku] = item;
          break;
        }
        // present in last XML edition
        case 'applicationSku': {
          this.catalogFeatures[item.sku] = item;
          break;
        }
      }
    }

    this.assignFeaturesToPlans()
  }

  assignFeaturesToPlans() {
    for (let i in this.catalogPlans) {
      let item = this.catalogPlans[i];
      let planFeatures = [];

      for (let f of item.features) {
        planFeatures.push(this.catalogFeatures[f]);
      }
      if (this.catalogPlans[i]) {
        this.catalogPlans[i].planFeatures = planFeatures;
      }
    }
  }
}



