import {Component, OnInit} from '@angular/core';
import {FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import {UserService} from '../../services/user.service';
import {Observable} from "rxjs/Observable";
import {ServerDataService} from '../../services/server-data.service';

@Component({
  selector: 'app-roles-and-users',
  templateUrl: './roles-and-users.component.html',
  styleUrls: ['./roles-and-users.component.css']
})
export class RolesAndUsersComponent implements OnInit {
  roles = [];
  selectedRoles = [];
  loader = false;

  secondFormGroup: FormGroup;
  myControl: FormControl = new FormControl();

  filteredOptions: Observable<string[]>;


  constructor(private userSys: UserService,
              private _serverDataService: ServerDataService,
              private _formBuilder: FormBuilder) {
  }

  ngOnInit() {
    this.userSys.getRoles().subscribe(response => this.roles = response.result);
    this.secondFormGroup = this._formBuilder.group({

    });

    this.filteredOptions = this.myControl.valueChanges
      .startWith(null)
      .map(val => val ? this.filter(val) : this.roles.slice());
  }


  filter(val: string): string[] {
    return this.roles.filter(role =>
      role.name.toLowerCase().indexOf(val.toLowerCase()) === 0);
  }

  onRoleSelect(role) {
    if (this.selectedRoles.indexOf(role) == -1) {
      this.selectedRoles.push(role);
    }
  }

  saveDetails() {
    // this.thirdFormGroup.disable();
    console.log('saveDetails start 1')
    var data: any = {};
    data.street = '62 Ave';
    data.state = 'NY';
    data.city = 'New York';
    data.zip = '00000';
    data.contactPerson = 'Vasiliy'

    this._serverDataService.saveBillingDetails(data)
    //   .subscribe(
    //   result => {}
    // );
  }

}



