import {Component, OnInit} from '@angular/core';
import {FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import {Response} from '@angular/http';

import {SettingService} from '../../services/setting.service';

@Component({
  selector: 'app-billing-accounts',
  templateUrl: './billing-accounts.component.html',
  styleUrls: ['./billing-accounts.component.css']
})
export class BillingAccountsComponent implements OnInit {
  ban: string;
  ctn: string;

  loader = false;

  billingAccounts = [];
  visibleNewForm = false;

  fourthFormGroup: FormGroup;

  constructor(private _formBuilder: FormBuilder,
              private globalSettings: SettingService) {
  }

  ngOnInit() {
    this.getbillingAccounts();
    this.fourthFormGroup = this._formBuilder.group({
      banCtrl: ['', Validators.required],
      ctnCtrl: ['', Validators.required]
    });
  }

  getbillingAccounts() {
    this.globalSettings.getBillingAccount()
      .subscribe(
        (response: Response) => {
          let data = response.json();
          if (data && data.result) {
            this.billingAccounts = data.result;
          }
        },
        error => {
        }
      );
    this.loader = false;
  }

  showNewForm() {
    this.visibleNewForm = true;
  }

  addBan() {
    this.loader = true;

    let data: any = {};
    data.ban = this.ban;
    data.ctn = this.ctn;

    this.globalSettings.validateBillingAccount(data).subscribe(
      result => {
        this.globalSettings.setBillingAccount(data).subscribe(
          success => {
            this.visibleNewForm = false;
            this.getbillingAccounts();
          }
        )
      }
    )
    this.loader = false;
  }

  removeBan(ban) {
    this.loader = true;
    this.globalSettings.removeBillingAccount(ban.sys_id).subscribe(
      result => {
        this.getbillingAccounts();
      }
    )
  }

}



