import {Injectable} from '@angular/core';
import {Http, Headers, Response} from '@angular/http';
import 'rxjs/add/operator/map';
import {BehaviorSubject} from 'rxjs/BehaviorSubject';
import {Observable} from 'rxjs/Observable';
import {Subject} from "rxjs/Subject";


@Injectable()
export class SettingService {
  private messageSource = new BehaviorSubject<string>('no sys_id');
  currentSysId = this.messageSource.asObservable();

  constructor(private http: Http) {
  }

  changeSysId(message: string) {
    this.messageSource.next(message)
  }


  getSysId() {
    let sysId = '';
    this.currentSysId.subscribe(response => sysId = response);
    return sysId;
  }

  createAuthorizationHeader(headers: Headers) {
    // headers.append('Authorization', 'Basic ' +
    //   btoa('admin:admin')
    // );
  }

  checkActivation() {
    return this.http
      .get('/api/now/table/x_mobi_aom_att_global_settings?sysparm_fields=sys_id%2Cactivation_state&sysparm_limit=1&sysparm_query=ORDERBYDESCsys_created_on');
  }

  getRequestInfo() {
    return this.http
      .get('/api/now/table/x_mobi_aom_att_global_settings?sysparm_limit=1&sysparm_query=ORDERBYDESCsys_created_on');
  }

  getSettings() {
    return this.http.get('/api/now/table/x_mobi_aom_att_global_settings?sysparm_fields=account_number%2Ccustomer_email%2Ccompany_name%2Ccustomer_name&sysparm_limit=1')
      .map(response => response.json());
  }

  setSettings(settings) {
    return this.http.post('/api/now/table/x_mobi_aom_att_global_settings', {
      'account_number': settings.accountNumber,
      'company_name': settings.companyName,
      'customer_name': settings.customerName,
      'customer_email': settings.customerEmail,
      'activation_state': 'submited'
      // 'is_activated': true
    });
  }

  setBillingInfo(data) {
    const sys_id = this.getSysId();
    return this.http.post('/api/x_mobi_aom/config/' + sys_id + '/billing', data);
  }

  validateBillingAccount(data) {
    const sys_id = this.getSysId();
    return this.http.get('/api/x_mobi_aom/config/' + sys_id + '/accounts/' + data.ban + '/lines/' + data.ctn);
    // return this.http.post('/api/now/table/x_mobi_aom_billing_account_numbers?sysparm_fields=', {
    //   'account_number': sys_id,
    //   'ban': data.ban,
    //   'ctn': data.ctn
    // });
    // /api/x_mobi_aom/config/{configId}/billing
  }

  setBillingAccount(data) {
    const sys_id = this.getSysId();
    return this.http.post('/api/now/table/x_mobi_aom_billing_account_numbers?sysparm_fields=', {
      'account_number': sys_id,
      'ban': data.ban,
      'ctn': data.ctn
    });
  }



  getBillingAccount() {
    let sys_id = this.getSysId();
    return this.http.get('/api/now/table/x_mobi_aom_billing_account_numbers?sysparm_limit=100&account_number=' + sys_id);
  }

  removeBillingAccount(sys_id) {
    return this.http.delete('/api/now/table/x_mobi_aom_billing_account_numbers/' + sys_id + '');
  }

  haveToken() {
    const sys_id = this.getSysId();
    return this.http.put('/api/now/table/x_mobi_aom_att_global_settings/' + sys_id +
      '?sysparm_fields=', {
      'activation_state': 'activated'
    }).map(
      response => {
        let result = response.json();
        if (result.result && result.result.configId) {
          this.changeSysId(result.result.configId);
        }
        // console.log('response', result.result.configId)
      }
    );
  }

  setToken(token, decodedToken) {
    // let sys_id = this.getSysId();
    // // sendDataToServer(decodedToken) ??
    // return this.http.put('/api/now/table/x_mobi_aom_att_global_settings/'
    //   + sys_id +
    //   '?sysparm_fields=', {
    //   'token_api': token,
    //   'tenant_code': decodedToken.tenantCode,
    //   'tenant_user': decodedToken.tenantUser,
    //   'tenant_password': decodedToken.tenantPassword
    // });
  }

  saveTokenAndPassword(data) {
    // SYS_ID need to SAVE as RESULT OF trunsaction !!!!!!
    // this.changeSysId();
    return this.http.post('/api/x_mobi_aom/config', data)
      .map(
        response => {
          let result = response.json();
          if (result.result && result.result.configId) {
            this.changeSysId(result.result.configId);
          }
          // console.log('response', result.result.configId)
        }
      );
  }


  checkCatalog() {
    const sys_id = this.getSysId();
    return this.http.get('/api/x_mobi_aom/catalog/' + sys_id);
    // return this.http.get('/api/now/table/x_mobi_aom_jsonfordevice?sysparm_fields=catalog&sysparm_limit=1')
  }

  pullCatalog() {
    const sys_id = this.getSysId();
    return this.http.post('/api/x_mobi_aom/catalog/' + sys_id, {});
    // return this.http.get('/api/now/table/x_mobi_aom_jsonfordevice?sysparm_fields=catalog&sysparm_limit=1')
  }
}



