import {Component, OnInit, ViewChild, Inject, Output, EventEmitter} from '@angular/core';
import {FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import {ServerDataService} from '../../services/server-data.service';
import {SettingService} from '../../services/setting.service';
import {Http, Response} from '@angular/http';

import {Observable} from 'rxjs/Observable';
import 'rxjs/add/operator/startWith';
import 'rxjs/add/operator/map';

@Component({
  selector: 'app-billing-details',
  templateUrl: './billing-details.component.html',
  styleUrls: ['./billing-details.component.css']
})
export class BillingDetailsComponent implements OnInit {
  street: string;
  state: string;
  city: string;
  zip: string;
  firstName: string;
  lastName: string;
  email: string;
  phoneNumber: string;

  loader = false;

  thirdFormGroup: FormGroup;

  @Output() billingDataReady = new EventEmitter<{ billingInfo: object }>();

  constructor(private _formBuilder: FormBuilder,
              private http: Http,
              private globalSettings: SettingService,
              private _serverDataService: ServerDataService) {
  }

  ngOnInit() {
    this.thirdFormGroup = this._formBuilder.group({
      streetCtrl: [''],
      stateCtrl: [''],
      cityCtrl: [''],
      zipCtrl: [''],
      firstNameCtrl: [''],
      lastNameCtrl: [''],
      emailCtrl: [''],
      phoneNumberCtrl: ['']
    });
  }

  saveDetails() {
    this.loader = true;
    // this.thirdFormGroup.disable();

    const billingInfo: any = {};
    billingInfo.address = this.street;
    billingInfo.state = this.state;
    billingInfo.city = this.city;
    billingInfo.zip = this.zip;
    billingInfo.contactFirstName = this.firstName;
    billingInfo.contactLastName = this.lastName;
    billingInfo.contactEmail = this.email;
    billingInfo.contactPhone = this.phoneNumber;

    this.globalSettings.setBillingInfo(billingInfo).subscribe(
      result => {
        this.billingDataReady.emit({billingInfo});
        this.loader = false;
      },
      error => {
        this.loader = false;
      }
    );

    // this._serverDataService.saveBillingDetails2(billingInfo)
    //   .subscribe(
    //   result => {}
    // );
  }

}



