import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import { HttpModule } from '@angular/http';
import {FlexLayoutModule} from "@angular/flex-layout";

import { MyCustomMaterialModule } from './___temp/material2.module';


import { AppComponent } from './app.component';
import { HeaderComponent } from './header/header.component';
import { LogoComponent } from './header/logo/logo.component';
import { BodyActivationComponent } from './body-activation/body-activation.component';
import { BodyStepperComponent } from './body-stepper/body-stepper.component';
import { DeviceDialogComponent } from './body-stepper/device-dialog/device-dialog.component';
import { SettingService } from './services/setting.service';
import { ServerDataService } from './services/server-data.service';
import { UserService } from './services/user.service';
import { RolesAndUsersComponent } from './body-stepper/roles-and-users/roles-and-users.component';
import { BillingDetailsComponent } from './body-stepper/billing-details/billing-details.component';
import { BillingAccountsComponent } from './body-stepper/billing-accounts/billing-accounts.component';
import { CatalogImportComponent } from './body-stepper/catalog-import/catalog-import.component';
import { ApiActivationComponent } from './body-stepper/api-activation/api-activation.component';
import { BodyActivatedComponent } from './body-activated/body-activated.component';


@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    LogoComponent,
    BodyActivationComponent,
    BodyStepperComponent,
    DeviceDialogComponent,
    RolesAndUsersComponent,
    BillingDetailsComponent,
    BillingAccountsComponent,
    CatalogImportComponent,
    ApiActivationComponent,
    BodyActivatedComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    FlexLayoutModule,
    ReactiveFormsModule,
    MyCustomMaterialModule,
    BrowserAnimationsModule
  ],
  entryComponents: [
    DeviceDialogComponent,
  ],
  providers: [
    DeviceDialogComponent,
    SettingService,
    ServerDataService,
    UserService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



