import {Component, Inject} from '@angular/core';
import {MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import {FormControl, FormBuilder, FormGroup, Validators} from "@angular/forms";

@Component({
  selector: 'app-device-dialog',
  templateUrl: './device-dialog.component.html',
  styleUrls: ['./device-dialog.component.css']
})
export class DeviceDialogComponent {

  device: any [];
  myGroup: FormGroup;

  plans = [];

  features = [];
  groupedFeatures = [];
  showLoader = true;


  constructor(public dialogRef: MatDialogRef<DeviceDialogComponent>,
              private _formBuilder: FormBuilder,
              @Inject(MAT_DIALOG_DATA) public data: any) {
    this.device = data;

    this.myGroup = this._formBuilder.group({
      nameCtrl: [data.name, Validators.required],
      skuCtrl: [data.sku, Validators.required],
      priceCtrl: [data.price, Validators.required],
    });
  }

  onNoClick(): void {
    this.dialogRef.close();
  }

  showPlanOptions(plan) {
    if (plan.isVisible) {
      //this.groupedFeatures = [];
      return false
    }
    this.showLoader = true;
    this.groupFeatures(plan.planFeatures)
    plan.isVisible = true;
  }


  groupFeatures(planFeatures) {
    this.groupedFeatures = [];
    let grouped = {};
    for (let feature of planFeatures) {
      // feature.groupId = 0;

      let replaceNumbers = feature.name.replace(/[0-9]/g, '');
      feature.isActive = false;
      if (feature.name.length != 0) {
        let hash = 0;
        for (let i = 0; i < replaceNumbers.length; i++) {
          let char = replaceNumbers.charCodeAt(i);
          hash = ((hash << 5) - hash) + char;
          hash = hash & hash;
        }
        // console.log('-- hash: ', hash)
        if (hash < 0) {
          hash = hash * -1;
        }
        if (!grouped.hasOwnProperty(hash)) {
          grouped[hash] = [];
        }
        grouped[hash].push(feature);
      }

    }

    for (let idx in grouped) {
      this.groupedFeatures.push(grouped[idx])
    }
    this.showLoader = false;
    //console.log('groupedFeatures', this.groupedFeatures)
    // this.groupedFeatures.sort()
  }

}



